import React, {Fragment, useEffect, useState} from "react";
import CommodityCard from "./commodityCard";
import FilterBox from "./filterBox";
import {getCommodities} from "../service/httpService";
import {toast} from "react-toastify";
import {useSearchParams} from "react-router-dom";

const CommoditiesPage = ({onChangedCartItem}) => {

    const [data, setData] = useState([])


    const [searchParams, _] = useSearchParams();
    const availableOnly = searchParams.get("available") == null ? false : searchParams.get("available") === "true"
    const sortBy = searchParams.get("sortBy") == null ? "name" : searchParams.get("sortBy")
    const search = searchParams.get("search")
    const searchBy = searchParams.get("by")

    useEffect(() => {
        getCommodities(
            search ?? "", sortBy ?? "name", searchBy ?? "name", availableOnly ?? false
        ).then(response => {
            if (response.status === 200)
                setData(response.data)
            else if (response.status !== 0)
                toast.error(response.data.message)
        })
    }, [search, sortBy, searchBy, availableOnly]);

    return (
        <Fragment>
            <FilterBox availableOnly={availableOnly} sortBy={sortBy}/>
            <ul className="commodity-card-flow gap-4 list-unstyled mt-4">
                {data.map((d) => (
                    <CommodityCard key={d.id} data={d}
                                   onChangedCartItem={onChangedCartItem}/>
                ))}
            </ul>
        </Fragment>
    )

}


export default CommoditiesPage