import React, {useState} from 'react';
import CartButton from "./cartButton";
import {useNavigate} from "react-router-dom";

const CommodityCard = ({data, onChangedCartItem}) => {
    const navigate = useNavigate()
    const [inCart, setInCart] = useState(data.inCart);
    const leftInStock = data.inStock
    return (
        <li>
            <div className="card product-card">
                <div className="card-body">
                    <h5 className="card-title text-primary-emphasis" role="button"
                        onClick={() =>
                            navigate("/commodities/" + data.id)
                        }>
                        {data.name}
                        </h5>
                    <h6 className="card-subtitle text-danger mb-">{leftInStock} left in stock</h6>
                    <img className="card-img" src="/static/img/nova9.png" alt="nova 9 picture"  role="button"
                         onClick={() =>
                             navigate("/commodities/" + data.id)
                         }/>
                    <div className="d-flex align-items-center justify-content-between">
                        <div className="card-text text-primary-emphasis fw-semibold d-inline">{data.price}$</div>
                        <CartButton inCart={inCart}
                                    setInCart={(c) => {
                                        setInCart(c)
                                        onChangedCartItem(data.id, c)
                                    }}
                                    leftInStock={leftInStock}
                                    commodityId={data.id}/>
                    </div>
                </div>
            </div>
        </li>
    );
}

export default CommodityCard