import React, {useState} from "react";
import {login} from "../service/httpService";
import {useLocation, useNavigate, useSearchParams} from "react-router-dom";
import {toast} from "react-toastify";

const LoginForm = ({onLoggedIn}) => {

    const [username, setUsername] = useState("")
    const [password, setPassword] = useState("")
    const navigate = useNavigate();
    const [searchParams, setSearchParams] = useSearchParams();


    function onLoginClicked() {
        const redirect = searchParams.get("redirect")
        login(username, password)
            .then((r) => {
                if (r.status === 200) {
                    onLoggedIn()
                    navigate(redirect == null ? "/" : redirect)
                } else if (r.status === 401)
                    toast.error(r.data.message)
            })
    }

    return (<div className="modal-content align-items-center">
            <img src="/static/icon/icon-balout.svg" className="m-4"/>
            <h4 className="modal-title lead">Login to your account</h4>
            <div className="modal-body">
                <form className="form align-items-center" onSubmit={(e) => e.preventDefault()}>
                    <div className="form-group align-items-center">
                        <input value={username}
                               onChange={({target}) => setUsername(target.value)}
                               type="text" className="form-control border-1 bg-isabelline no-outline m-1"
                               name="username"
                               placeholder="Username" required="required"/>
                    </div>
                    <div className="">
                        <input value={password}
                               onChange={({target}) => setPassword(target.value)}
                               type="password" className="form-control border-1 bg-isabelline no-outline m-1"
                               name="password" placeholder="Password"
                               required="required"/>
                    </div>
                    <div className="form-group justify-content-center my-2">
                        <button onClick={onLoginClicked} type="submit"
                                className="btn btn-primary m-1 text-center">Login
                        </button>
                    </div>
                </form>
            </div>
        </div>
    );

};

export default LoginForm
