import React, {Fragment} from 'react';
import {useSearchParams} from "react-router-dom";

export default function FilterBox({availableOnly, sortBy}) {

    const [searchParams, setSearchParams] = useSearchParams();

    const setAvailableOnly = (enabled) => {
        searchParams.set("available", enabled)
        setSearchParams(searchParams)
    }

    const sortBys = ["name", "price"]

    const setSortBy = (sortBy) => {
        searchParams.set("sortBy", sortBy)
        setSearchParams(searchParams)
    }

    return (
        <div className="d-flex align-items-center p-3" id="filter-box">
            <label className="form-check-label text-primary h4 me-3" htmlFor="availableCommodities">
                Available Commodities
            </label>
            <div className="form-check form-switch form-control-lg d-inline-block">
                <input className="form-check-input" type="checkbox" role="switch" id="availableCommodities"
                       checked={availableOnly}
                       onChange={() => {
                           setAvailableOnly(!availableOnly)
                       }}
                />
            </div>
            <div className="h5 d-inline-block ms-auto me-5">Sort By:</div>
            <div className="d-inline-block me-2" role="group">

                {
                    sortBys.map((by) =>
                        (
                            <Fragment>
                                <input type="radio" className="btn-check" name="options" id={"sort-" + by}
                                       autoComplete="off"
                                       checked={sortBy === by}
                                       onChange={() => {
                                           setSortBy(by)
                                       }}/>
                                <label className="btn btn-primary btn-radio" htmlFor={"sort-" + by}>{by}</label>
                            </Fragment>
                        ))
                }
            </div>
        </div>
    );
}
