import axios from "axios";
import {toast} from "react-toastify";
import data from "bootstrap/js/src/dom/data";

// const apiEndpoint = "https://84499176-30a8-41e8-a5f0-245aa4f77f53.mock.pstmn.io/"
const apiEndpoint = "http://localhost:9090/api/"
export const tokenStorage = sessionStorage

axios.interceptors.request.use(config => {
    try {
        config.headers.authToken = tokenStorage.getItem('token');
    } catch (e) {
    }
    return config
})

axios.interceptors.response.use(null, error => {
    const error400 = error.response && error.response.status >= 400 && error.response.status < 500

    if (error instanceof Error && error.message === "Network Error") {
        toast.error("Check your internet")
        error.response = {status: 0, data: {status: 0, message: "Check your internet"}}
    } else if (error.response.status === 500) {
        toast.error("Internal Server Error 500")
        console.log("Server Error: " + error.response.message)
    } else if (!error400) {
        toast.error("An unexpected error occurred")
        console.log("Unexpected Http Error: " + error)
    } else return error.response
    return {status: 0}
})

function url(api) {
    return apiEndpoint + api;
}

export async function getCommodities(query, sortBy, searchBy, available) {
    return axios.get(url("commodities"), {
            params: {
                "query": query,
                "sort-by": sortBy,
                "search-by": searchBy,
                "available": available
            }
        }
    );
}

export async function login(username, password) {
    const data = {
        "username": username,
        "password": password
    }
    const res = await axios.post(url("login"), data)
    if (res.status === 200) {
        tokenStorage.setItem('token', res.data.authToken)
        console.log("set token")
        tokenStorage.setItem('username', username)
    }
    return res
}

export async function register(username, password, email, address, birthdate) {
    const data = {
        "username": username,
        "password": password,
        "email": email,
        "address": address,
        "birthdate": birthdate
    }
    const response = await axios.post(url("register"), data)
    if (response.status === 200) {
        tokenStorage.setItem('token', response.data.authToken)
        tokenStorage.setItem('username', username)
    }
    return response
}

export async function getUser() {
    const username = tokenStorage.getItem('username')
    return axios.get(url("users/" + username));
}

export async function addCredit(amount) {
    return axios.post(url("addCredit"), {amount: amount});
}

export async function getShoppingList() {
    return axios.get(url("shoppingList"));
}

export async function addToCart(commodityId) {
    return axios.post(url("shoppingList/add"), {
        "commodityId": commodityId,
        "count": 1
    });
}

export async function removeFromCart(commodityId) {
    return axios.post(url(`shoppingList/remove`), {
        "commodityId": commodityId,
        "count": 1
    });
}


export async function getHistoryItems() {
    return axios.get(url("purchasedList"));
}

export async function getCommodity(commodityId) {
    return axios.get(url(`commodities/${commodityId}`));
}

export async function rateCommodity(commodityId, rating) {
    return axios.post(url("commoditiesRates"), {
        commodityId: commodityId,
        rating: rating
    });
}

export async function getComments(commodityId) {
    return axios.get(url("comments?commodityId=" + commodityId));
}

export async function getSingleComment(commentId) {
    return axios.get(url("comments/" + commentId));
}

export function voteComment(commentId, vote) {
    return axios.post(url("commentsVotes"), {
        commentId: commentId,
        vote: vote
    })
}

export function postComment(commodityId, text) {
    return axios.post(url("comments"), {
        commodityId: commodityId,
        text: text
    })
}

export function pay(discount) {
    return axios.get(url("pay?discountCode=" + discount))
}

export function getDiscount(discount) {
    return axios.get(url(`discount?discountCode=${discount.trim()}`))
}

export function getProvider(providerId){
    return axios.get(url(`providers?providerId=${providerId}`))
}
export function getProvidersCommodities(providerId){
    return axios.get(url(`providers/commodities?providerId=${providerId}`))
}
export function getRecommended(commodityId){
    return axios.get(url(`recommended?commodityId=${commodityId}`))
}