import React from 'react';
import {addCredit} from "../service/httpService";
import {toast} from "react-toastify";

export default function AddCreditDialog({amount, onSuccess}) {
    return (
        <div className="modal fade" id="addCreditModal" data-bs-keyboard="false" tabIndex="-1"
             aria-labelledby="staticBackdropLabel" aria-hidden="true">
            <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content p-3" style={{maxWidth: "350px"}}>
                    <div className="modal-body">
                        <h4>Add Credit</h4>
                        <p className="text-primary fs-4 fw-semibold">
                            Are you sure you want to add ${amount + " "}to your account
                        </p>
                    </div>
                    <div className="d-flex justify-content-end gap-3">
                        <button type="button" className="btn btn-outline-danger" data-bs-toggle="modal"
                                data-bs-target="#addCreditModal">
                            Close
                        </button>
                        <button type="button" className="btn btn-primary"
                                data-bs-toggle="modal" data-bs-target="#addCreditModal"
                                onClick={() => {
                                    addCredit(amount).then(r => {
                                        if (r.status === 200)
                                            onSuccess()
                                        else if (r.status !== 0)
                                            toast.error(r.data.message)
                                    })
                                }}>
                            Confirm!
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
