import React, {useState} from "react";
import ld from "lodash"
import {useNavigate} from "react-router-dom";
import {register} from "../service/httpService";
import {toast} from "react-toastify";

const RegisterForm = () => {
    const days = ld.range(1, 32)
    const months = ["January", "February", "March",
        "April", "May", "June",
        "July", "August", "September",
        "October", "November", "December"
    ]
    const years = ld.range(2023, 1900)
    const [username, setUsername] = useState("")
    const [password, setPassword] = useState("")
    const [email, setEmail] = useState("")
    const [address, setAddress] = useState("")
    const [birthday, setBirthday] = useState(days[0])
    const [birthMonth, setBirthMonth] = useState(months[0])
    const [birthYear, setBirthYear] = useState(years[0])
    const navigate = useNavigate();

    function onRegisterClicked() {
        register(username, password, email, address, `${birthYear}-${birthMonth}-${birthday}`)
            .then(r => {
                if (r.status === 200)
                    navigate("/")
                else if (r.status !== 0)
                    toast(r.message)
            })
    }

    return (
        <div className="modal-content align-items-center">
            <img src="/static/icon/icon-balout.svg" className="m-4"/>
            <h4 className="modal-title lead">Sign up!</h4>
            <div className="modal-body">
                <form className="form align-items-center" onSubmit={(e) => e.preventDefault()}>
                    <div className="form-group align-items-center">
                        <label className="form-label">Username</label>
                        <input value={username}
                               onChange={({target}) => setUsername(target.value)}
                               type="text" className="form-control border-1 bg-isabelline no-outline m-1"
                               placeholder="Username" required="required"/>
                    </div>
                    <div className="">
                        <label className="form-label">Password</label>
                        <input value={password}
                               onChange={({target}) => setPassword(target.value)}
                               type="password" className="form-control border-1 bg-isabelline no-outline m-1"
                               placeholder="Password"
                               required="required"/>
                    </div>
                    <div>
                        <label className="form-label">Email</label>
                        <input value={email}
                               onChange={({target}) => setEmail(target.value)}
                               type="email" className="form-control border-1 bg-isabelline no-outline m-1"/>
                    </div>

                    <div className="row">
                        <div className="col col-3">
                            <label htmlFor="birthdate-day" className="form-label">Day</label>
                            <select value={birthday}
                                    onChange={({target}) => setBirthday(target.value)}
                                    className="form-select border-1 bg-isabelline no-outline m-1" id="birthdate-day"
                                    name="birthdate-day">
                                {
                                    days.map(day => (
                                            <option value={day}>{day}</option>
                                        )
                                    )
                                }
                            </select>
                        </div>
                        <div className="col col-5">
                            <label htmlFor="birthdate-month" className="form-label">Month</label>
                            <select value={birthMonth}
                                    onChange={({target}) => setBirthMonth(target.value)}
                                    className="form-select border-1 bg-isabelline no-outline m-1" id="birthdate-month"
                                    name="birthdate-month">
                                {
                                    months.map(i => (
                                        <option>{i}</option>
                                    ))
                                }
                            </select>
                        </div>
                        <div className="col col-4">
                            <label htmlFor="birthdate-year" className="form-label">Year</label>
                            <select value={birthYear}
                                    onChange={({target}) => setBirthYear(target.value)}
                                    className="form-select border-1 bg-isabelline no-outline m-1" id="birthdate-year"
                                    name="birthdate-year">
                                {
                                    years.map(year => (
                                            <option value={year}>{year}</option>
                                        )
                                    )
                                }
                            </select>
                        </div>
                    </div>
                    <div>
                        <label className="form-label">Address</label>
                        <textarea value={address}
                                  onChange={({target}) => setAddress(target.value)} rows="3" cols="30"
                                  className="form-control border-1 bg-isabelline no-outline m-1"
                                  placeholder="Address"/>
                    </div>
                    <div className="justify-content-center my-2">
                        <button onClick={onRegisterClicked} type="submit" className="btn btn-primary m-1 align-self-center bt">Register
                        </button>
                    </div>

                </form>
            </div>
        </div>
    );

};

export default RegisterForm
