import React, {useState} from 'react';
import {getComments, getSingleComment, voteComment} from "../service/httpService";

export default function Comment({comment: initComment}) {

    const [comment, setComment] = useState(initComment)

    function updateComment() {
        getSingleComment(comment.commentId).then(r => {
            if (r.status === 200)
                setComment(r.data)
        })
    }

    function onLike() {
        const vote = comment.usersVote === 1 ? 0 : 1
        voteComment(comment.commentId, vote)
            .then(r => {
                if (r.status === 200)
                    updateComment()
            })
    }

    function onDislike() {
        const vote = comment.usersVote === -1 ? 0 : -1
        voteComment(comment.commentId, vote)
            .then(r => {
                if (r.status === 200)
                    updateComment()
            })
    }

    const likeClassName = (comment.usersVote === 1) ? "filter-green" : ""
    const dislikeClassName = (comment.usersVote === -1) ? "filter-red" : ""

    return (
        <li className="comment-card p-2 my-2">
            <h4 className="mt-3">{comment.text}</h4>
            <div className="text-gray fs-6 d-inline-block">{comment.date}</div>
            <div className="text-gray fs-4 d-inline-block px-3"></div>
            <div className="text-gray fs-6 d-inline-block">#{comment.username}</div>
            <div className="d-flex w-100 align-items-center justify-content-end">
                <div className="text-primary d-inline-block">Is this comment helpful?</div>
                <button className="btn btn-icon d-inline-block">{comment.likes}
                    <img onClick={onLike}
                         className={likeClassName}
                         src="/static/icon/icon-thumbs-up.svg"
                         alt="Like"/>
                </button>
                <button className="btn btn-icon d-inline-block me-5">{comment.dislikes}
                    <img onClick={onDislike}
                         className={dislikeClassName}
                         src="/static/icon/icon-thumbs-down.svg"
                         alt="Dislike"/>
                </button>
            </div>
        </li>
    );
}
