import React, {Fragment, useEffect, useState} from 'react';
import CartButton from "./cartButton";
import PurchaseDialog from "./purchaseDialog";
import {getShoppingList} from "../service/httpService";
import {toast} from "react-toastify";
import {useNavigate} from "react-router-dom";


function CartHeader(props) {
    return (
        <div className="w-100 text-primary fs-1 fw-semibold m-4 ms-0 d-flex align-items-center gap-2">
            <img className="size-icon" src="/static/icon/icon-cart.svg" alt=""/>
            Cart
        </div>
    );
}

function PayButton({inCart}) {

    return (
        <div className="w-100 text-center">
            <button id="pay-now" className="btn btn-primary btn-wide my-4" data-bs-toggle="modal"
                    disabled={inCart === 0}
                    data-bs-target="#purchaseModal">
                Pay now!
            </button>
        </div>
    );
}

function CartItem({index, item, onChangedCartItem}) {
    const rowStart = index + 2
    const [inCart, setInCart] = useState(item.count);
    const navigate = useNavigate()
    return (
        <Fragment>
            <div style={{gridColumn: "1 / 9", gridRowStart: rowStart, alignSelf: "normal", justifySelf: "normal"}}
                 className="white-card"/>

            <div role="button" style={{gridColumnStart: 1, gridRowStart: rowStart}} className="fw-semibold text-gray m-1"
                 onClick={() => navigate("/commodity/" + item.commodityId)}>
                <img  className="thumbnail-img" src="/static/img/nova9.png" alt=""/>
            </div>
            <div role="button" style={{gridColumnStart: 2, gridRowStart: rowStart}} className="fw-semibold text-gray"
                 onClick={()=>navigate("/commodity/"+item.commodityId)}>
                {item.name}
            </div>
            <div style={{gridColumnStart: 3, gridRowStart: rowStart}} className="fw-semibold text-gray">
                {item.categories.join(", ")}
            </div>
            <div style={{gridColumnStart: 4, gridRowStart: rowStart}} className="fw-semibold text-gray">
                ${item.price}
            </div>
            <div role="button" style={{gridColumnStart: 5, gridRowStart: rowStart}} className="fw-semibold text-gray p-3"
                 onClick={() => navigate("/providers/" + item.providerId)}>
                {item.providerId}
            </div>
            <div style={{gridColumnStart: 6, gridRowStart: rowStart}} className="fw-semibold  text-warning fs-5">
                {item.rating}
            </div>
            <div style={{gridColumnStart: 7, gridRowStart: rowStart}} className="fw-semibold text-danger fs-5">
                {item.inStock}
            </div>
            <div style={{gridColumnStart: 8, gridRowStart: rowStart}} className="fw-semibold">
                <CartButton inCart={inCart}
                            setInCart={(c) => {
                                setInCart(c)
                                onChangedCartItem(item.commodityId, c)
                            }}
                            leftInStock={item.inStock}
                            commodityId={item.commodityId}
                            onChangedCartItem={onChangedCartItem}/>
            </div>
        </Fragment>
    );
}


export default function CartItems({onChangedCartItem}) {

    const [shoppingList, setShoppingList] = useState([])

    function onGetShoppingList() {
        getShoppingList().then(r => {
            if (r.status === 200)
                setShoppingList(r.data)
            else if (r.status !== 0)
                toast.error(r.data.message)
        })
    }

    useEffect(() => {
        onGetShoppingList()
    }, [])


    return (<Fragment>
            <CartHeader/>
            <div
                style={{
                    display: "grid",
                    alignItems: "center",
                    justifyItems: "center",
                    gridRowGap: "10px",
                    gridTemplateColumns: "max-content auto auto auto auto auto auto auto"
                }}
                className="w-100  mt-3">
                <div style={{gridColumn: "1 / 9", gridRowStart: 1, alignSelf: "normal", justifySelf: "normal"}}
                     className="white-card"/>
                <div style={{gridColumnStart: 1, gridRowStart: 1}} className="fw-semibold p-2">Image</div>
                <div style={{gridColumnStart: 2, gridRowStart: 1}} className="fw-semibold p-2">Name</div>
                <div style={{gridColumnStart: 3, gridRowStart: 1}} className="fw-semibold p-2">Categories</div>
                <div style={{gridColumnStart: 4, gridRowStart: 1}} className="fw-semibold p-2">Price</div>
                <div style={{gridColumnStart: 5, gridRowStart: 1}} className="fw-semibold p-2">Provider ID</div>
                <div style={{gridColumnStart: 6, gridRowStart: 1}} className="fw-semibold p-2">Rating</div>
                <div style={{gridColumnStart: 7, gridRowStart: 1}} className="fw-semibold p-2">In Stock</div>
                <div style={{gridColumnStart: 8, gridRowStart: 1}} className="fw-semibold p-2">In Cart</div>

                {
                    shoppingList.map((item, i) => (
                            <CartItem key={item.productId} index={i} item={item} onChangedCartItem={onChangedCartItem}/>
                        )
                    )
                }

            </div>

            <PayButton inCart={shoppingList.length}/>

            <PurchaseDialog shoppingList={shoppingList}/>
        </Fragment>
    );
}
