import React, {useEffect, useState} from 'react';
import CartButton from "./cartButton";
import {rateCommodity} from "../service/httpService";
import {toast} from "react-toastify";
import {useNavigate} from "react-router-dom";

export default function ProductInfo({commodity, inCart, setInCart, onChangedCartItem}) {

    const navigate = useNavigate()
    const [totalRating, setTotalRating] = useState({rating: commodity.rating, rateCount: commodity.rateCount});
    const [rating, setRating] = useState(0);

    useEffect(() => {
        setTotalRating(commodity)
    }, [commodity])

    return (
        <div className="d-flex flex-wrap">
            <div className="d-inline-block ">
                <img src="/static/img/nova9.png" alt="Picture of nova 9"/>
            </div>
            <div className="flex-grow-1 d-inline-block ms-4">
                <h1 className="text-primary-emphasis fw-semibold">{commodity.name}</h1>
                <div className="d-flex justify-content-between">
                    <h5 className="text-danger">{commodity.inStock} left in stock</h5>
                    <div>
                        <img src="/static/icon/icon-star-filled.svg" alt="stars" className="d-inline-block size-icon"/>
                        <div id="stars"
                             className="d-inline-block text-body-emphasis fw-semibold">{totalRating.rating}</div>
                        <div className="d-inline-block text-gray fw-medium">(<span
                            id="rated_count">{totalRating.rateCount}</span>)
                        </div>
                    </div>
                </div>
                <h5 className="">by <a onClick={(event => {
                    event.preventDefault()
                    navigate("/providers/" + commodity.providerId)
                })} href="#">{commodity.providerName}</a></h5>
                <h5 className="text-primary">Categories</h5>
                <ul className="text-primary">
                    {
                        commodity.categories.map((category) => (
                            <li>{category}</li>
                        ))
                    }
                </ul>
                <div className="d-flex justify-content-between align-items-center add-to-cart-card p-4 pt-2 pb-2">
                    <div className="text-primary-emphasis fw-semibold d-inline fs-2">${commodity.price}</div>
                    <CartButton
                        inCart={inCart}
                        setInCart={(c) => {
                            setInCart(c)
                            onChangedCartItem(commodity.id, c)
                        }}
                        commodityId={commodity.id} leftInStock={commodity.inStock}/>
                </div>
                <div className="d-grid  p-4"
                     style={{"grid-template-rows": "auto auto", "grid-template-columns": "auto 100px"}}>
                    <div className="fw-semibold fs-5"
                         style={{"grid-column": "1 / span 1,", "grid-row": "1 / span 1"}}>rate now
                    </div>
                    <div className="pe-4" style={{"grid-column": "1 / span 1", "grid-row": "2 / span 1"}}>
                        <StarRating
                            rating={rating}
                            setRating={setRating}/>
                    </div>
                    <button
                        onClick={() =>
                            rateCommodity(commodity.id, rating)
                                .then(res => {
                                    if (res.status === 200) {
                                        setTotalRating(res.data)
                                        toast("Thank you for rating!")
                                    } else
                                        toast.error("Rating failed!")
                                })
                        }
                        className="btn btn-primary"
                        style={{"grid-column": "2 / span 1", "grid-row": "1 / span 2", "align-self": "center"}}
                    >
                        submit
                    </button>
                </div>
            </div>
        </div>
    );
}

const StarRating = ({rating, setRating}) => {
    const [hover, setHover] = useState(0);
    return (
        <div className="star-rating">
            {[...Array(10)].map((star, index) => {
                index += 1;
                const classN = index <= (hover || rating) ? "star-on" : "star-off"
                return (
                    <img
                        type="button"
                        src="/static/icon/icon-star-filled.svg" alt="10 stars"
                        key={index}
                        className={classN + " d-inline-block size-icon rating-star "}
                        // className={index <= (hover || rating) ? "star-on" : "star-off"}
                        onClick={() => setRating(index)}
                        onMouseEnter={() => setHover(index)}
                        onMouseLeave={() => setHover(rating)}
                    />
                );
            })}
        </div>
    );
};