import React, {useState} from 'react';
import {postComment} from "../service/httpService";

export default function AddCommentBox({commodity, onPosted}) {

    const [text, setText] = useState("")

    function onPostComment() {
        console.log(commodity)
        postComment(commodity.id, text)
            .then(r => {
                if (r.status === 200) {
                    onPosted()
                    setText("")
                }
            })
    }

    return (
        <div className="comment-card p-4">
            <h3 className="text-primary my-1">Submit your opinion</h3>
            <div className="d-flex align-items-end mt-2">
                <textarea
                    value={text}
                    onChange={({target}) => setText(target.value)}
                    className="flex-grow-1 text-box p-2 fw-medium"/>
                <button
                    onClick={onPostComment}
                    className="btn btn-primary ms-4">Post
                </button>
            </div>
        </div>
    );
}
