import React, {Fragment} from 'react';
import {addToCart, removeFromCart} from "../service/httpService";

export default function CartButton({commodityId, inCart, setInCart, leftInStock}) {

    function onAdd() {
        inCart = inCart + 1
        setInCart(inCart)
        addToCart(commodityId).then(r => {
            if (r.status == 200)
                setInCart(r.data.count)
            else setInCart(inCart - 1)
        })
    }

    function onRemove() {
        inCart = inCart - 1
        setInCart(inCart)
        removeFromCart(commodityId).then(r => {
            if (r.status == 200)
                setInCart(r.data.count)
            else setInCart(inCart + 1)
        })
    }

    return (<Fragment>
        {inCart === 0 &&
            <button className="btn btn-outline-primary" disabled={leftInStock < 1} onClick={onAdd}>
                add to cart
            </button>}
        {inCart > 0 && <div
            className="btn outline-primary p-0 min-w-100 d-inline-flex justify-content-between align-items-center">
            <button className="btn btn-icon h-100" onClick={onRemove}>-
            </button>
            <div className="">{inCart}</div>
            <button className="btn btn-icon h-100" disabled={inCart === leftInStock}
                    onClick={onAdd}>+
            </button>
        </div>}
    </Fragment>);
}

