import React, {Fragment, useEffect, useState} from 'react';
import ProductInfo from "./productInfo";
import Comment from "./comment";
import AddCommentBox from "./addCommentBox";
import CommodityCard from "./commodityCard";
import {getComments, getCommodity, getRecommended} from "../service/httpService";
import {toast} from "react-toastify";
import {useParams} from "react-router-dom";
import {useCallback} from "react";

export default function CommodityPage({onChangedCartItem}) {

    const [commodity, setCommodity] = useState({
        id: 0,
        name: "Loading",
        providerId: 0,
        providerName: "Loading",
        price: 0,
        categories: [
            "Loading"
        ],
        rating: 0,
        rateCount: 0,
        inStock: 0,
        inCart: 0
    })

    const [comments, setComments] = useState([])
    const [recommended, setRecommended] = useState([])
    const [inCart, setInCart] = useState(commodity.inCart)

    const params = useParams();


    useEffect(() => {
        getCommodity(params.id).then(r => {
            if (r.status === 200) {
                setCommodity(r.data)
                setInCart(r.data.inCart)
            } else if (r.status !== 0)
                toast.error(r.data.message)
        })
    }, [params.id])


    const onGetComments = useCallback(() => {
        getComments(params.id).then(r => {
            if (r.status === 200)
                setComments(r.data)
            else if (r.status !== 0)
                toast.error(r.data.message)
        })
    }, [params.id])

    const onGetRecommended = useCallback(() => {
        getRecommended(params.id).then(r => {
            if (r.status === 200)
                setRecommended(r.data)
            else if (r.status !== 0)
                toast.error(r.data.message)
        })
    }, [params.id])


    useEffect(() => {
        onGetComments()
        onGetRecommended()
        window.scrollTo(0, 0)
    }, [onGetComments, onGetRecommended, commodity.id])

    return (
        <Fragment>
            <ProductInfo commodity={commodity} inCart={inCart} setInCart={setInCart}
                         onChangedCartItem={onChangedCartItem}/>
            <div>
                <h3 className="text-primary mt-3">Comments<span className="text-gray fs-3">(<span
                    id="commentCount">{comments.length}</span>)</span>
                </h3>
                <ul className="list-unstyled">
                    {comments.map((comment) => (
                        <Comment comment={comment}/>
                    ))}
                </ul>
            </div>
            <AddCommentBox commodity={commodity} onPosted={onGetComments}/>

            <h3 className="text-primary my-4">You might also like...</h3>
            {/*<ul className="d-flex flex-wrap justify-content-between list-unstyled">*/}
            <ul className="commodity-card-flow gap-4 list-unstyled mt-4">
                {recommended.map((d) => (
                    <CommodityCard key={d.id} data={d}
                                   onChangedCartItem={onChangedCartItem}/>
                ))}
            </ul>
            {/*</ul>*/}
        </Fragment>
    );
}
