import logo from './logo.svg';
import './App.css';
import PrimaryButton from "./components/primaryButton";
import Navbar from "./components/navbar";
import Footer from "./components/footer";
import {Fragment, useEffect, useState} from "react";
import LoginForm from "./components/loginForm";
import {BrowserRouter, Route, Routes, useSearchParams} from "react-router-dom";
import RegisterForm from "./components/registerForm";
import CommoditiesPage from "./components/commoditiesPage";
import CommodityPage from "./components/commodityPage";
import ProfilePage from "./components/profilePage";
import {ToastContainer} from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import {PrivateRoute} from "./components/privateRoute";
import {getShoppingList, tokenStorage} from "./service/httpService";
import ProviderPage from "./components/providerPage";

function App() {

    const [cart, setCart] = useState(new Set())

    function updateShoppingList() {
        getShoppingList().then(r => {
            if (r.status === 200)
                setCart(new Set(r.data.map(item => item.commodityId)))
        })
    }

    useEffect(() => {
        if (tokenStorage.getItem('username') != null)
            updateShoppingList()
    }, [])

    const onChangedCartItem = (id, count) => {
        console.log("onChangedCartItem")
        if (count > 0) {
            cart.add(id)
            setCart(new Set(cart))
        } else {
            cart.delete(id)
            setCart(new Set(cart))
        }
    }


    return (
        <BrowserRouter>
            <ToastContainer/>
            <Navbar cart={cart.size}/>
            <main className="container container-fluid border-4 below-header">
                <Routes>
                    <Route path="/" element={
                        <PrivateRoute>
                            <CommoditiesPage
                                onChangedCartItem={onChangedCartItem}/>
                        </PrivateRoute>
                    }
                    />
                    <Route path="/login" element={<LoginForm onLoggedIn={updateShoppingList}/>}/>
                    <Route path="/register" element={<RegisterForm/>}/>
                    <Route path="/commodities" element={
                        <PrivateRoute>
                            <CommoditiesPage
                                onChangedCartItem={onChangedCartItem}/>
                        </PrivateRoute>
                    }/>
                    <Route path="/commodities/:id" element={
                        <PrivateRoute>
                            <CommodityPage onChangedCartItem={onChangedCartItem}/>
                        </PrivateRoute>
                    }/>
                    <Route path="/profile" element={
                        <PrivateRoute>
                            <ProfilePage onChangedCartItem={onChangedCartItem}/>
                        </PrivateRoute>
                    }>
                    </Route>
                    <Route path="/providers/:id" element={
                        <PrivateRoute>
                            <ProviderPage onChangedCartItem={onChangedCartItem}/>
                        </PrivateRoute>
                    }/>
                </Routes>
            </main>
            <Footer/>
        </BrowserRouter>
    );
}

export default App;
