import React, {useEffect, useState} from "react";
import {Link, useLocation, useNavigate, useParams, useSearchParams} from "react-router-dom";
import {tokenStorage} from "../service/httpService";
import {toast} from "react-toastify";

const Navbar = ({cart}) => {

    const searchBys = ["name", "category"]
    const username = tokenStorage.getItem('username')
    const location = useLocation()

    const [searchParams, setSearchParams] = useSearchParams();
    const searchBy = searchParams.get("by") == null ? "name" : searchParams.get("by")
    const setSearchBy = (by) => {
        searchParams.set("by", by)
        setSearchParams(searchParams)
    }
    const [search, setSearch] = useState(searchParams.get("search"))

    const navigate = useNavigate()

    function logout() {
        tokenStorage.removeItem('token')
        tokenStorage.removeItem('username')
        navigate("/")
        toast("logged out!")
    }

    const btnClass = (cart > 0) ? "btn-primary" : "btn-secondary"

    function onSearchClicked() {
        searchParams.set("search", search)
        setSearchParams(searchParams)
    }

    return (
        <nav className="navbar navbar-light bg-light fixed-top shadow">
            <div className="container-fluid">
                <Link className="navbar-brand d-flex align-items-center" to="/">
                    <img src="/static/icon/icon-balout.svg" height="30" width="auto"
                         className="d-inline-block m-1 align-top "/>
                    <span className="navbar-brand text-primary fw-semibold">Baloot</span>
                </Link>

                {/*     search bar      */}
                {(location.pathname === "/commodities" || location.pathname === "/"|| location.pathname === "/commodities/") &&
                    <form className="align-self-center nav-item bg-isabelline round-border"
                          onSubmit={(event => event.preventDefault())}>
                        <div className="btn-group">
                            <button type="button" className="btn btn-secondary dropdown-toggle"
                                    data-bs-toggle="dropdown"
                                    aria-expanded="false">
                                {searchBy}
                            </button>
                            <ul className="dropdown-menu">
                                {searchBys.map((by => (
                                    <li><a onClick={(e) => {
                                        e.preventDefault()
                                        setSearchBy(by)
                                    }} className="dropdown-item" href="">{by}</a>
                                    </li>
                                )))}
                            </ul>
                        </div>

                        <input value={search}
                               onChange={({target}) => setSearch(target.value)}
                               onKeyPress={event => {
                                   if (event.key === 'Enter') {
                                       onSearchClicked()
                                   }
                               }}
                               className="border-0 no-outline bg-isabelline text-gray search-box" placeholder="Search"/>
                        <img onClick={onSearchClicked}
                             className="btn-icon size-icon-sm mx-2" src="/static/icon/icon-search.svg" alt="search"/>
                    </form>

                }

                {
                    username === null &&
                    <div className="d-flex order-last">
                        <button onClick={() => navigate("/login")} className="btn btn-secondary m-1"
                                type="submit">Login
                        </button>
                        <button onClick={() => navigate("/register")} className="btn btn-secondary m-1"
                                type="submit">Register
                        </button>
                    </div>
                }
                {
                    username !== null &&
                    <div className="d-flex order-last align-items-center">
                        <div onClick={logout} className="text-primary m-1 link-primary fs-5 fw-bold me-3"
                             type="submit">#{username}</div>
                        <button onClick={() => navigate("/profile")}
                                className={"btn m-1 " + btnClass} type="submit">
                            Cart
                            <span className="ms-3" id="in-cart">
                                {cart}
                            </span>
                        </button>
                    </div>
                }
            </div>
        </nav>
    );
};

export default Navbar;