import React, {useState} from 'react';
import {getDiscount, pay} from "../service/httpService";
import {toast} from "react-toastify";
import {toNumber} from "lodash/lang";

export default function PurchaseDialog({shoppingList}) {


    function totalPrice() {
        const withoutDC = shoppingList.map((i) => i.count * i.price).reduce((a, b) => a + b, 0)
        return withoutDC - (withoutDC * toNumber(discountValue)/100)
    }

    const [discount, setDiscount] = useState("")

    const [discountValue, setDiscountValue] = useState(0)
    const [discountValid, setDiscountValid] = useState(0)

    function onPay() {
        pay(discount).then(r => {
            if (r.status === 200)
                window.location.reload();
            else if (r.status !== 0)
                toast.error(r.data.message)
        })
    }

    function onApplyDiscount() {
        getDiscount(discount).then(r=>{
            if (r.status === 200){
                if (r.data["code-status"] === "valid"){
                    setDiscountValue(r.data.discount)
                    setDiscountValid(1)
                } else {
                    setDiscountValue(0)
                    setDiscountValid(-1)
                }
            }
        })
    }

    let inputClass = " d-inline-block text-box-discount fw-bold ";
    if (discountValid === 0)
        inputClass += ""
    else if (discountValid === 1)
        inputClass += "text-success"
    else
        inputClass += "text-danger"


    return (
        <div className="modal fade" id="purchaseModal" data-bs-keyboard="false" tabIndex="-1"
             aria-labelledby="staticBackdropLabel" aria-hidden="true">
            <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content p-3" style={{maxWidth: "250px"}}>
                    <div className="modal-body">
                        <h4>Your cart</h4>
                        <ul className="p-2">
                            {
                                shoppingList.map((item) => (
                                    <li className="d-flex justify-content-between">
                                        <div>
                                            {item.commodityName} x {item.count}
                                        </div>
                                        <div className="text-primary">
                                            ${item.price}
                                        </div>
                                    </li>
                                ))
                            }
                        </ul>
                        <form className="d-flex justify-content-between p-1"
                        onSubmit={(e)=>e.preventDefault()}>
                            <input value={discount}
                                   onChange={({target}) => setDiscount(target.value)}
                                   className={inputClass} type="text"
                                   placeholder="discount code" name="discountCode"/>
                            <button
                                onClick={() => onApplyDiscount()}
                                className="btn btn-primary d-inline-block">Submit
                            </button>
                        </form>
                        <div className="d-flex justify-content-between">
                            <div className="text-primary fw-semibold">total</div>
                            <div className="text-danger fw-semibold">${totalPrice()}</div>
                        </div>
                    </div>
                    <div className="d-flex justify-content-end gap-3">
                        <button type="button" className="btn btn-outline-danger" data-bs-toggle="modal"
                                data-bs-target="#purchaseModal">
                            Close
                        </button>
                        <button type="button" className="btn btn-primary"
                                onClick={() => {
                                    onPay()
                                }}>
                            Buy!
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
