import React, {Fragment, useEffect, useState} from 'react';
import CommodityCard from "./commodityCard";
import {getProvider, getProvidersCommodities} from "../service/httpService";
import {useParams} from "react-router-dom";

export default function ProviderPage({onChangedCartItem}) {

    const params = useParams()
    const [provider, setProvider] = useState({name: "Loading"})
    const [providedCommodities, setProvidedCommodities] = useState([])

    function onGetCommodities() {
        getProvidersCommodities(params["id"])
            .then(r => {
                if (r.status === 200)
                    setProvidedCommodities(r.data)
            })
    }

    function onGetProvider() {
        console.log("getting provider")
        getProvider(params["id"])
            .then(r => {
                    console.log(r.data)
                if (r.status === 200) {
                    setProvider(r.data)
                }
            })
    }


    useEffect(() => {
        onGetProvider()
        onGetCommodities()
    }, [])


    return (<Fragment>
            <div className="d-flex justify-content-center">
                <div className="" style={{width: "min-content"}}>
                    <img src="/static/img/Huawei.png" style={{height: "350px"}}/>
                    <p className="ms-auto text-end text-primary fw-semibold">since 1990</p>
                    <h1 className="text-primary fw-bold">{provider.name}</h1>
                </div>
            </div>
            <h3 className="text-primary mt-3">All provided commodities</h3>
            <ul className="commodity-card-flow gap-4 list-unstyled mt-4">
                {providedCommodities.map((d) => (
                    <CommodityCard key={d.id} data={d}
                                   onChangedCartItem={onChangedCartItem}/>
                ))}
            </ul>
        </Fragment>
    );
}
