import React, {useEffect, useState} from 'react';
import CartItems from "./cartItems";
import AddCommentBox from "./addCommentBox";
import AddCreditDialog from "./addCreditDialog";
import {getShoppingList, getUser} from "../service/httpService";
import {useNavigate} from "react-router-dom";
import {toast} from "react-toastify";
import data from "bootstrap/js/src/dom/data";

export default function UserInfo(props) {

    const [user, setUser] = useState({
        username: "loading", email: "loading", birthDate: "loading", address: "loading", credit: "loading"
    })


    function onGetUser() {
        getUser().then(r => {
                if (r.status === 200)
                    setUser(r.data)
                else if (r.status !== 0)
                    toast.error(r.data.message)
            }
        )
    }


    useEffect(() => {
        onGetUser()
    }, [setUser])

    const [amount, setAmount] = useState("")

    function onCreditAdded() {
        setAmount("")
        onGetUser()
    }

    return (
        <div className="white-card d-flex flex-wrap">
            <div className="col p-3 icon-grid">
                <img className="col-icon" src="/static/icon/icon-user.svg" alt="name"/> <h5
                className="m-0 ms-2" id="name">{user.username}</h5>
                <img className="col-icon" src="/static/icon/icon-mail.svg" alt="email"/> <h5
                className="m-0 ms-2" id="email">
                {user.email}</h5>
                <img className="col-icon" src="/static/icon/icon-calendar.svg" alt="birthdate"/> <h5
                className="m-0 ms-2" id="birthdate">
                {user.birthDate}</h5>
                <img className="col-icon" src="/static/icon/icon-location.svg" alt="address"/> <h5
                className="m-0 ms-2" id="address">{user.address}</h5>
            </div>
            <form className="p-5">
                <div className="d-block text-primary text-credit w-100 my-2" id="credit">${user.credit}</div>
                <input value={amount}
                       onChange={({target}) => setAmount(target.value)}
                       className="d-block text-box-add-credit w-100 my-2" type="number" placeholder="$Amount"
                       name="amount" autoComplete="false"/>
                <button type="button" disabled={amount === ""}
                        className="btn btn-primary d-block w-100 my-2" data-bs-toggle="modal"
                        data-bs-target="#addCreditModal">Add More Credit
                </button>

                <AddCreditDialog amount={amount} onSuccess={onCreditAdded}/>
            </form>
        </div>
    );
}
